@import "https://fonts.googleapis.com/css2?family=Lato&display=swap";
@import "https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Lato, sans-serif;
}

a {
  color: #fff;
  width: auto;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

#desktop {
  width: 100%;
  height: 120%;
  object-fit: cover;
  opacity: .8;
  z-index: -1;
  position: fixed;
  top: 0;
}

body {
  margin: 0;
}

section {
  padding: 0;
  overflow: hidden;
}

h1 {
  letter-spacing: .1rem;
  text-transform: uppercase;
  font-size: 3.75em;
}

h2 {
  letter-spacing: .1rem;
  text-transform: uppercase;
  font-size: 2.5rem;
}

@media screen and (max-width: 1490px) {
  h2 {
    font-size: 2.25em;
  }
}

@media screen and (max-width: 920px) {
  h2 {
    font-size: 2.25em;
  }
}

@media screen and (max-width: 784px) {
  h2 {
    font-size: 2em;
  }
}

h3 {
  font-size: 2.25rem;
}

