.navLinks {
  align-items: center;
  padding-right: 1.125rem;
  list-style: none;
  display: flex;
}

.navLinks #register {
  color: #e9e9e9;
  opacity: .8;
  text-shadow: .125rem .125rem .125rem #00000040;
  transition: all .4s;
  transition: .2;
  background: #5ac348;
  border-radius: 1px;
  margin: 0 1.5625rem;
  padding: .625rem 1.875rem;
  text-decoration: none;
  display: inline-block;
}

@media screen and (max-width: 784px) {
  .navLinks #register {
    display: none;
  }
}

.navLinks #register:hover {
  color: #222;
  opacity: 1;
  text-shadow: .125rem .125rem .125rem #00000040;
  transition: all .4s;
  transition: .2;
  background-color: #1dd200;
  border-radius: 1px;
}

.navLinks .navItem {
  margin: 0 .1875rem 0 0;
  transition: all .4s;
}

@media screen and (max-width: 784px) {
  .navLinks .navItem {
    display: none;
  }
}

.navLinks .navItem a {
  color: #e9e9e9;
  color: #fff;
  text-shadow: .1875rem .1875rem .1875rem #00000040;
  transition: .2;
  border: 1px solid #ffffff80;
  border-radius: .125rem;
  margin: 0;
  padding: .625rem 1.875rem;
  text-decoration: none;
  display: flex;
}

.navLinks .navItem:hover {
  opacity: 1;
  text-shadow: .125rem .125rem .125rem #00000040;
  transition: all .4s;
  transition: .2;
  background-color: #4682b4;
  border-radius: 1px;
}

.loginLinks {
  align-items: bottom;
  justify-content: center;
  list-style: none;
  display: flex;
}

