footer {
  height: 470px;
  width: 100%;
  background-color: #0e294b;
  margin-top: 40px;
}

footer .blackFooter {
  height: 130px;
  width: 100%;
  background-color: #222;
  border-bottom: 5px solid #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

footer .blackFooter .contact {
  justify-content: center;
  align-items: center;
  display: flex;
}

footer .blackFooter .contact p {
  color: #fff;
  padding: 5px 10px 0;
  font-size: 2rem;
}

footer .blueFooter {
  width: 100%;
  height: 72%;
  text-align: center;
  color: #fff;
  flex-direction: row;
  place-content: flex-end space-between;
  align-items: flex-end;
  padding: 1% 8%;
  display: flex;
}

footer .blueFooter ul {
  min-width: 8rem;
  text-align: left;
  text-align: center;
  flex-direction: column;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 784px) {
  footer .blueFooter ul {
    display: none;
  }
}

footer .blueFooter ul:first-child {
  text-align: left;
}

footer .blueFooter ul:last-child {
  text-align: right;
}

footer .blueFooter ul li {
  width: 11rem;
  height: 1.875rem;
  margin: 0;
  padding: 0;
  text-decoration: underline;
}

footer .blueFooter ul li:last-child {
  margin-bottom: .3125rem;
  padding-bottom: 1.25rem;
}

nav {
  height: 4.375rem;
  width: 100%;
  z-index: 2;
  text-shadow: 0 .75rem .5625rem #0006;
  transition: 0;
  background-color: #0e294b;
  border-radius: .125rem;
  margin: 2.1875rem 0 0;
  padding-left: 2.1875rem;
  padding-right: .625rem;
  display: flex;
  position: absolute;
}

