  align-items: center;
  padding-right: 1.125rem;
  list-style: none;
  display: flex;
}

nav .navLinks li {
  display: flex;
}

nav .navLinks li h2 {
  color: #4682b4;
  padding-left: .625rem;
}

nav .navLinks li .logo {
  width: 100%;
}

nav .navLinks li .logo img {
  width: 8rem;
  vertical-align: middle;
  justify-content: center;
  padding: 0 0 .625rem 2.8125rem;
  transition: all .4s;
}

