  }
}

@media screen and (max-width: 784px) {
  nav .navLinks .search {
    display: none;
  }
}

nav .navLinks .search .input {
  border-width: .125rem;
  border-radius: .25rem;
  padding: .625rem 1.25rem .625rem .625rem;
  position: relative;
  box-shadow: inset .375rem .375rem .75rem #0006;
}

nav .navLinks .carrito {
  opacity: .7;
  width: 2.5rem;
  transition: all .4s;
}

nav .navLinks .carrito:hover {
  opacity: 1;
  width: 2.5rem;
  transition: all .4s;
}

nav .navLinks .burgerMenu {
  max-height: 42px;
  width: auto;
  padding-left: 15px;
}

nav .navLogo {
  justify-content: left;
}

nav .dropdown {
  display: inline-block;
  position: relative;
}

nav .dropdown:hover .dropdown-content {
  display: block;
}

nav .dropdown .dropdown-content {
  z-index: 1;
  min-width: 250px;
  background-color: #f9f9f9;
  display: none;
  position: absolute;
  right: 0;
}

