  color: #0e294b;
  text-align: center;
  padding: 16px 10px;
  font-size: 20px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

nav .dropdown .dropdown-content a:hover {
  color: #e9e9e9;
  background-color: #0e294b;
  font-weight: 600;
}

